@import url("https://fonts.googleapis.com/css2?family=Charis+SIL:ital,wght@0,400;0,700;1,700&family=Dancing+Script:wght@700&family=Josefin+Sans&family=Lobster&family=Oxygen+Mono&family=Oxygen:wght@300;400;700&family=Poppins:ital,wght@0,900;1,900&family=Shizuru&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background-color: #F4E2DE;
  height: 100vp;
}

/* ----------------------------------Designing navigation bar ------------------------------------------ */
a {
  cursor: pointer;
}

nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
  padding: 10px;
  background-color: #F4E2DE;
  box-shadow: 0px 0px 5px #FFB9AB;
}
nav .nav-img {
  margin-top: 0;
}
nav .navbar ul {
  position: relative;
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  gap: 4rem;
  text-transform: uppercase;
}
nav .navbar ul li {
  position: relative;
}
nav .navbar ul li a {
  display: block;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 600;
  color: #850818;
}
nav .navbar ul li a:hover {
  font-size: 1.1rem;
  transition: 0.3s ease;
}
nav .navbar ul li a.active::after, nav .navbar ul li a:hover::after {
  content: " ";
  position: absolute;
  bottom: -6px;
  left: 0px;
  width: 110%;
  height: 3px;
  border-radius: 50%;
  background-color: #850818;
  opacity: 0.5;
}
nav .navbar ul li:nth-child(6) a span {
  font-size: 30px;
}
nav #hamburgur {
  display: none;
}
nav #social {
  display: none;
}

#hero {
  position: relative;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 1fr);
  margin-top: 1rem;
  margin-bottom: 2rem;
}
#hero .front {
  grid-column: 2/7;
  grid-row: 1/12;
  order: 1;
  display: grid;
  justify-content: flex-start;
  align-items: flex-end;
  background-color: #FFF9F7;
  line-height: 150%;
  font-size: large;
}
#hero .front #sale {
  width: 250px;
}
#hero .front .center {
  padding-left: 3em;
  padding-bottom: 3em;
}
#hero .front .center a {
  position: relative;
  display: inline-block;
  padding: 10px 30px;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
}
#hero .front .center a span {
  display: block;
  position: absolute;
  background: black;
}
#hero .front .center a span:nth-child(1) {
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.5s;
}
#hero .front .center a span:nth-child(2) {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  transform: scalex(0);
  transform-origin: left;
  transition: transform 0.5s;
}
#hero .front .center a span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 2px;
  height: 100%;
  transform: scaleY(0);
  transform-origin: bottom;
  transition: transform 0.5s;
}
#hero .front .center a span:nth-child(4) {
  top: 0;
  right: 0;
  width: 100%;
  height: 2px;
  transform: scalex(1);
  transform-origin: left;
  transition: transform 0.5s;
}
#hero .front .center a:hover {
  background-color: rgb(0, 0, 0);
  color: white;
  transition: background-color 1s ease-in, color 1s ease-in;
}
#hero .front .center a:hover span:nth-child(1) {
  transform: scaleY(1);
  transform-origin: bottom;
  transition: transform 0.5s;
}
#hero .front .center a:hover span:nth-child(2) {
  transform: scaleX(1);
  transform-origin: right;
  transition: transform 0.5s;
}
#hero .front .center a:hover span:nth-child(3) {
  transform: scaleY(1);
  transform-origin: top;
  transition: transform 0.5s;
}
#hero .front-cover {
  grid-column: 6/12;
  grid-row: 2/13;
  order: 2;
  top: 50%;
}
#hero .front-cover img {
  width: 100%;
  box-shadow: 0 0px 10px 10px #F4E2DE;
}

#services {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  margin-bottom: 2rem;
  gap: 2%;
}
#services .card {
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.3);
  padding: 1em;
  text-align: center;
}
#services .card img {
  display: block;
  width: 200px;
  height: 180px;
}
#services .card h5 {
  display: inline;
  font-size: 1em;
  border-radius: 15px;
  line-height: 20px;
  padding: 4px 20px;
  background-color: white;
}

#best-seller, #shop {
  position: relative;
  background-color: #F4E2DE;
}
#best-seller h4, #shop h4 {
  text-transform: uppercase;
  text-align: center;
  font-size: 1em;
  margin-bottom: 1rem;
  padding-top: 1rem;
}
#best-seller hr, #shop hr {
  margin: 0 auto;
  width: 60%;
  border-top: 1px solid rgb(0, 0, 0);
  background-color: #807C7C;
}
#best-seller .grid, #shop .grid {
  max-height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 200px);
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
#best-seller .grid .best, #shop .grid .best {
  margin-top: 2rem;
  margin-bottom: 2rem;
  grid-column: span 1;
}
#best-seller .grid .best .img, #shop .grid .best .img {
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  box-shadow: 0 0 5px 2px rgba(136, 136, 136, 0.8);
}

#shop {
  background-color: #DDD8D7;
}
#shop h4 {
  text-transform: uppercase;
  text-align: center;
  font-size: 1em;
  margin-bottom: 1rem;
  padding-top: 1rem;
}
#shop hr {
  margin: 0 auto;
  width: 60%;
  border-top: 1px solid rgb(0, 0, 0);
  background-color: #A3857F;
}
#shop .shopping {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  grid-template-rows: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
#shop .shopping .best {
  margin-top: 2rem;
  margin-bottom: 2rem;
  grid-column: span 1;
}
#shop .shopping .best .img {
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  box-shadow: 0 0 5px 2px rgba(136, 136, 136, 0.8);
}

/* ------------------------------media query for medium devices(752px)------------------------------ */
@media only screen and (max-width: 752px) {
  nav {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
  }
  nav #hamburgur {
    display: block;
  }
  nav #social {
    display: block;
  }
  nav .nav-img {
    flex-basis: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  nav #navbar {
    flex-basis: 100%;
  }
  nav #navbar ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: row wrap;
  }
  nav #navbar ul li {
    text-align: center;
    flex-basis: 42%;
    line-height: 80px;
  }
  nav #social {
    flex-basis: 100%;
  }
  nav #social .social-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
    gap: 2%;
  }
  nav #social .social-icon li {
    list-style-type: none;
  }
  nav #social .social-icon li a img {
    width: 20px;
  }
  .menu {
    position: absolute;
    transform: translateX(-1000px);
    transform: translateY(-1000px);
  }
  .menu2 {
    position: absolute;
    transform: translateX(-1000px);
    transform: translateY(-1000px);
  }
}
/* ------------------------------media query for small devices(582px)------------------------------ */
@media only screen and (max-width: 582px) {
  #navbar ul li {
    flex-basis: 100%;
  }
}